import { Fragment } from "react";
import  stotra  from '../Images/stotra.jpg'
import saptarishi from '../Images/saptarishi.jpg'

const Home = () => {
    return <Fragment>
        <div className="mf-mainDiv">
            <img src={saptarishi} alt="Saptarishi" className="saptarishi"/>
            <img src={stotra} alt="Stotra" className="stotra" />
            <h2>We are part of nature. Our health is what we eat. We respect every living being in nature
            and understand that we all are a part of eosystem. We use natural farming techniques to grow
            food. </h2>
            <h2>Our Vision is to follow Indian </h2>
            <h2>Our Mission is to bring back the sustainable agriculture</h2>
            </div>
   </Fragment>
        
}
export default Home;                                                                 